const blog = {
    readmore:       "Lire la suite",
    title01:        "Le travail à distance",
    date01:         "10-06-2016",
    content01:      "<p><b>La transformation digitale d'une entreprise passe aussi par le travail à distance.</b> Cette tendance est beaucoup plus importante qu’une simple mode car elle s’inscrit dans ce mouvement de fond qu’est l’<b>ubérisation</b>.</p><p> Le principe du travail horaire présente de multiples contraintes pour l’employeur et l’employé. L'employeur doit, par exemple, assumer les coûts parfois très élevés des surfaces de bureau nécessaires pour ses salariés. Quant à l'employé, il est souvent contraint de se lever très tôt pour accéder à un lieu de travail qui n'a parfois aucune incidence sur la réalisation de son travail et sur son efficacité. </p> <p> <b >Tout d’abord, qu’entendons-nous par « travail à distance » ? </b> </p> <p> Il ne s’agit pas, comme en sont persuadés certains managers, de laisser chez eux les collaborateurs d’une entreprise, sans contrôle et donc d'avoir la sensation de « les payer à ne rien faire ». </p> <p> Bien au contraire, il s’agit de ne plus considérer une journée de travail comme une suite d’heures payées mais comme un temps d’efficacité consacré à la réalisation de missions objectives. Autrement dit, l’idée est de ne plus de payer une traditionnelle heure de travail mais de rémunérer un employé pour l’accomplissement d’une mission et l’atteinte d’objectifs. Peu importe le temps consacré, l’important est l’atteinte des objectifs. </p> <p> Au cas où le salarié parvient à accomplir une mission en une semaine au lieu de deux semaines préconisées, il s’agira d’un véritable bénéfice pour l’entreprise. Dès lors, le salarié aura fait avancé l’entreprise beaucoup plus rapidement, sera disponible pour d’autres missions et entretiendra surtout plus facilement sa motivation. </p> <p> <b >Le manager est-il en droit de s’interroger sur le lieu de travail ? </b> </p> <p> Avec le « travail à distance », le lieu géographique choisi par l’employé n’est plus du ressort de l’employeur. Que le salarié réalise sa mission dans un bureau « officiel », dans son salon ou au bord de la piscine, cela n’a plus d’importance. </p> <p> Cependant le manager doit pouvoir s’assurer à tout moment que les missions sont bien effectuées. En effet, le salarié perçoit naturellement son salaire à la fin du mois. </p> <p> Le « travail à distance » n’est en aucune manière un « laissez-faire sans contrôle ». Aujourd’hui il existe un certains nombres d’outils pour pouvoir communiquer aisément à distance et renforcer la collaboration à distance avec les employés : </p> <ul> <li> Skype : permet, dans sa version gratuite, d’organiser des vidéos conférence avec 5 collaborateurs simultanément. </li> <li> Cisco Web Ex : conçu pour les entreprises, il permet de gérer jusqu’à 100 personnes en vidéo conférence. De plus, il permet les échanges de fichiers pendant les réunions, l’enregistrement des réunions et leur planification. </li> <li> GoToMeeting : Sur le même principe que Cisco Web Ex mais avec un système de forfaits. </li> <li> Teamviewer : compatible avec la majorité des machines (pc ou mac), il permet le partage d’écran. </li> </ul> <p> <b >Tous les métiers sont-ils compatibles avec ce mode de fonctionnement ? </b> </p> <p> La réponse est non. Dès l’instant où le métier nécessite un véritable contact humain ou du matériel spécifique, autre qu’un pc et une connexion internet, cela n’est pas envisageable. Bon nombre de métiers ne sont pas adaptés à ce mode de travail : un DRH doit, par exemple, rester au cœur de l’entreprise pour gérer l’humain, les techniciens auprès de leurs machines pour une maintenance efficace... </p> <p> <b >Quels changements dans la relation manager/managé engendre le travail à distance ? </b> </p> <p> Travailler à distance nécessite une véritable relation de confiance entre le collaborateur et sa hiérarchie. Chacun doit respecter l’autre. Le collaborateur devra assumer efficacement les missions relatives à sa fonction, être discipliné pour entretenir son efficacité et ne pas se laisser déborder par sa vie privée. Pour le manager, il s’agira d’adopter d’entretenir la confiance instaurée, de renforcer la communication à l’aide des outils numériques disponibles sans pour autant se montrer intrusif. Naturellement, il s’agira également de respecter le cadre légal prévu par le code du travail. </p>",
    title02:        "Le savoir, sa transmission",
    date02:         "03-11-2016",
    content02:      "<p><b>Le savoir est essentiel à l’Homme</b></p><p>Depuis des millénaires, l’Homme aspire à la pérénnité de l’avancée techique et à la transmission du savoir. Cependant, ce savoir est immatériel et des transmetteurs humains ou matériels ont, de tout temps, été nécessaires pour l’enseigner à d’autres. Quelles sont les méthodes de transmission du savoir ? Comment la transmission du savoir a-t-elle évolué jusqu’à nos jours ?</p><p><b>La transmission orale</p></b> <p> Jusqu’à la démocratisation des systèmes d’écriture, la transmission de la mythologie, de la religion, des lois et du savoir s’effectue par le principe de la tradition orale. Cependant, la tradition orale pose le problème de l'authenticité de l’information retransmise. En effet, chacun peut adapter les propos en fonction de son intérêt ou de ses envies et engendrer une multitude de variantes de l’information de départ. Il n’est donc pas rare que l’information perde totalement son sens initial. Jusqu’aujourd’hui, la tradition orale reste l’une des principales formes d’éducation et l’un des principaux outils de transmission de savoir dans nos sociétés. </p> <p><b> La révolution de l’écriture </p></b> <p> Le développement des systèmes d'écriture permet de diffuser durablement le savoir et de constituer une véritable mémoire du passé. Officiellement, l’écriture est née à Sumer, en Mésopotamie, au IVe millénaire avant notre ère. Les premiers signes d’écriture apparaissent pour répondre à des besoins pratiques : comptabiliser le bétail ou les sacs de grains sur des tablettes. Plusieurs techniques se succèdent ensuite afin de propager le savoir. Les Égyptiens écrivent sur des rouleaux de papyrus, les Chinois sur des livres de bois et de soie et les romains sur du papyrus. <p/> <p> C'est à Alexandrie qu'est construite en 288 avant notre ère la première bibliothèque du monde. Elle est née du souhait de vouloir rassembler le savoir universel dans un seul endroit. </p> <p><b> L'imprimerie </p></b> </p> Jusqu'à l'invention de l'imprimerie par Gutenberg au XVI ème siècle, les livres sont soigneusement copiés à la main. C'était, par exemple, le travail des moines copistes qui reproduisaient de magnifiques enluminures. L'arrivée de l'imprimerie revolutionne la diffusion des livres et du savoir. En effet, les coûts engendrés pour la copie d'un ouvrage sont bien moins élevés et la reproduction est beaucoup plus rapide. Cependant, il faudra attendre encore très longtemps pour que le livre devienne accessible à tous. </p> <p> De nos jours, le livre a toujours une place prépondérante en tant que diffuseur de savoir et de support d'éducation pour les professeurs. </p> <p><b> Internet et la dématérialisation du savoir </p></b> <p> L’arrivée d’Internet dans les années 1990 est une véritable révolution pour le stockage de l’information et des savoirs. Des protocoles standardisés de transfert de données permettent aujourd’hui au monde entier de vivre « connecté », d’accéder à l’information et de profiter d’un nombre incroyable de services. Internet est certainement le premier outil à offrir une capacité de stockage quasi-illimitée des données. Les différents moteurs de recherche permettent à tout utilisateur de trouver l’information recherchée à l’aide de quelques mots-clés. Il n’y a, pour ainsi dire, plus aucun domaine pour lequel on ne trouverait pas d’information sur Internet. </p> <p><b> En conclusion </p></b> <p> Les techniques de transmission du savoir se sont perfectionnées au fur et à mesure de l’avancée technologique orchestrée par l’Homme. Aucune de ces techniques n’a aujourd’hui disparu au profit d’une autre mais continue à œuvrer pour une connaissance et une maîtrise toujours plus grande de l’univers. Le e-learning s’inscrit aujourd’hui dans une mouvance de dématérialisation du savoir et d’accès rapide, efficient et économique au savoir. Applicable dans de nombreux domaines, le e-learning apparaît comme une révolution pédagogique en s’adaptant aux contraintes des apprenants. L’avenir réserve en toute certitude de belles heures de gloire à cette nouvelle technique de transmission du savoir.</p> ",
    title03:        "Ce n'est pas la période qui fait l'emploi, mais l'emploi qui fait la période.",
    date03:         "01-06-2020",
    content03:      "<p>Depuis quelques temps j’entends pas mal de développeur junior, voir même intermédiaire, se plaindre de ne pas trouver de poste, supposément du à une <i>période compliqué</i>.</p> <p> Sans nier la particularité de ce temps <i>post-covid</i>, je dois dire que je reste assez dubitatif quand à cet argument; en effet je suis plutôt d'avis que nous vivons au contraire un temps d'opportunités incroyables ! </p> <p> Le confinement à démontré l'efficacité du remote et toute la puissance du numérique, la digitalisation des entreprises est croissante et du même coup les compétences en développement. Pour se convaincre du dynamisme du secteur, il suffit de regarder le nombre d'écoles et de centres de formation qui fleurissent; c'est aujourd'hui que les entreprises doivent investir sur les développeurs junior, aujourd'hui que les développeurs junior doivent expliquer aux recruteurs pourquoi ils ont besoin d 'eux ! </p> <p> Non ce n'est pas la période qui fait l'emploi, mais l'emploi qui fait la période. Alors bien sur tout n'est pas si facile, bien sur il ne suffit pas d'attendre pour que le travail vienne, mais ceci est de tout temps et en tout lieu; a chacun de faire preuve de talent - et de son talent -, <b>a toi, développeur junior un peu désespéré par les refus successifs, de montrer que tu ne cherche pas d'emploi mais que c'est l'emploi qui te cherche.</b></p>"   ,
    title04:        "Comment transformer l'essai à la sortie de ma formation ?",
    date04:         "09-07-2020",
    content04:      "<p>Chaque développeur junior, sortant de formation, s'est un jour posé la question: Comment me vendre dès lors que je n'ai encore rien à présenter ? </p> <p> Effectivement, le propre du métier de développeur est justement qu'il s'agit d'un métier qui ne s'appuie pas tant sur des diplômes que sur de réelles compétences qu'il s'agit de démontrer, le plus souvent via un portfolio (en opposition avec d'autres qui s'appuieront sur un cv). Seulement, sorti de formation, il est bien rare de posséder d'en posséder déja un, compte tenu de l'absence de première expérience. Comment dès lors démontrer ses compétences ? </p> <p> A un très jeune junior qui me poserai la question, je répondrai que la toute première chose à faire serai de <b>soigner son compte linkedin</b>; vous me direz que cela ne permet en rien de démontrer quoi que ce soit, détrompez-vous, ce profil est le reflet de ce que vous êtes et permet d'identifier vos soft-skill assez facilement: les posts que vous likez, les commentaires que vous laissez, les contenus que vous créez, sont des éléments qui permettent d'identifier une personnalité; une bonne gestion de ce compte est la première étape indispensable pour tout développeur junior sorti de formation, d'autant plus importante qu'il n'est pas aujourd'hui de recruteur qui ne cherche dans ce réseau social. </p> <p> Ceci fait, la seconde étape, et pas la moindre, est la <b>création du-dit portfolio</b>. Il faut donc répondre à la problématique du contenu, évoquée plus haut : Tout d'abord vous pouvez construire votre site en partant tout simplement de votre cv; très simplement une single page réalisée dans la/les technologies appris-es vous permettra déjà de démontrer ce que vous savez faire. Pourquoi ne pas l'agrémenter d'un formulaire de contact, voir d'une interface admin que vous pourriez présenter ? Ainsi, avec un contenu déjà maîtrisé, vous avez une démo fullstack à votre portée. Une fois ceci en ligne, vous pouvez vous lancer dans le développement de petits programmes simples qui commenceront a alimenter votre portfolio, par exemple, un simple Pong ou un Tic-Tac-Toe feront toujours leur effet, également pensez a ce que votre site soit au minimum responsive et pourquoi pas utilisez du PWA. </p> <p> <b>N'oubliez pas, si vous n'avez pas encore de première expérience, soyez positif et créez la !</b></p>",
    title05:       "Que mettre dans son premier portfolio ? ",
    date05:        "14-07-2020",
    content05:     "Pour tout développeur junior, la question s'est posé un jour: que mettre dans mon portfolio ? En effet, si vouloir en faire un est une première étape indispensable, le remplir est évidemment la suite logique, encore faut-il avoir quelque chose à y mettre, alors que aucune expérience professionnel ne peux encore l'enrichir. Il est important d'avoir en tête le bon paradigme: l'objectif n'est pas d'empiler fièrement des expériences professionnels, mais de montrer votre savoir faire; ceci posé il deviens tout de suite plus simple d'y voir clair, si les expériences professionnelles sont inexistantes en sortie de formation, votre savoir faire est lui bien présent; aussi, voici quelques exemples simples:<ul><li> un tic-tac-toe</li><li> un pong</li><li> une calculatrice</li><li> un blog avec une interface d'administration</li><li> une utilisation de google map (ou autre)</li><li> un site de recherche de bande annonce</li><li> un dashboard</li><li> etc .</li></ul>L'important en fait et de couvrir vos éléments de compétences: une application purement front (exemple: tic-tact-toe), une application avec une gestion backend et gestion de SQL (exemple: un blog avec une partie administration), une application front avec une gestion d'API Json (exemple: un site de recherche de bande annonce), une gestion d'API en back (exemple: une utilisation de google map), une démonstration de ce que vous savez faire en data analyse (exemple: un dashboard, en partant de données public), une démonstration de vos capacités css (exemple: une calculatrice). Tout ceci ne sont que des exemple, à vous d'en trouvez d'autres, l'important que vous puissiez dire: <i>j'ai appris ceci; la preuve.</i>.",
    title06:        "Un développeur junior peux-t-il se présenter comme un fullstack ?",
    date06:         "20-07-2020",
    content06:      "<p>Les formations pour devenir développeur web sont aussi variés que variable...</p><p> Pour les besoins de cette réflexion, nous allons retenir deux catégories: celles qui propose des formation spécifiques frontend ou backend, et celles qui proposes des formations fullstack, également nous allons nous baser sur des formations certifiantes et de durées égales, afin de rester sur une base solide.</p><p> Les deux catégories seront accès sur un choix de <i>techno</i> qui pourra varier selon la période, les organismes de formations cherchant, le plus souvent et avec raison, à être cohérente avec les besoins fluctuant du marchés.</p><p> Dans le premier cas, ce qui est proposé est de se concentrer sur un seul aspect du développement web, à savoir le frontend ou le backend.</p><p> Il est important, à ce stade, de rappeler que le métier de développeur web regroupe plusieurs aspects (je détail raisonnablement volontairement):</p><ul> 1 - Le développement frontend<li> Le <i>maquettage</i></li> <li>L'intégration</li>L <li>Le SEO</li></ul> <ul>2 - Le développement backend <li>Les bases de données</li> <li>Le code pur</li></ul> <ul>3 - La mise en production <li>Le déploiement</li> <li>La création de l'infra</li> </ul> <p> Les formations frontend font, en général, l'impasse sur la partie <i>maquettage</i>, dit <i>webdesign</i>, considérant souvent et à raison qu'il s'agit d'un métier à part entière.</p> <p>Ces formations permettent en principe de couvrir largement le sujet données, parfois même de manière assez précise. </p><p>Le second cas, les formations dites fullstack, prennent en compte, en général, le premier et le second point (le troisième n'étant souvent pas considéré comme étant du ressort du développeur web).</p><p> La toute première réflexion que l'on peux se faire et que, à durée égale, un sujet est forcément moins approfondis si il cohabite avec un autre. Cependant cela permet à l'apprenant d'avoir une vision plus global de son futur métier.</p><p>Cependant, dans un cas comme dans l'autre, on remarque également que le point trois, à savoir <i>la mise en production</i> est occulté.</p><p>Là, une question de se pose: peut-on se considérer fullstack si on occulte partie mise en production ? Deux choses l'une, soit on considère cet élément comme ne faisant pas partie du métier de développeur web, et dans ce cas on peux utiliser le terme de fullstack, soit on considère qu'un développeur web se doit d'être capable de faire toute la chaîne de production , c'est à dire de la prise en main du projet à sa mise en production, dans ce ca effectivement le terme fullstack peux se présenter comme étant un peu usurpé.</p><p> Néanmoins, comme rien n'est pas jamais aussi simple, toute les entreprises, tout les projets, ne demandent pas les même exigences. Sans vouloir couvrir toutes les options, ce qui serai trop long, on peux en citer trois qui me semble assez représentatifs : La startup, le grand groupe et le freelance.</p><p>La startup, avant sa première levé de fond, n'a en général que peu de budget, aussi bien souvent c'est le développeur backend ou le CTO qui assurera la mise en production, de ce fait le développeur junior nouvellement embauché n'aura pas à assurer cette partie délicate; le grand groupe ira vers la même conclusion mais pas pour les même raisons, ici il s'agira plutôt d'un soucis d'optimisation du temps de travail et des compétences; le freelance lui sera suullstack ?. Pour ma part je pense qu'il le peux, si ça certification l'indique, mais qu'il ne doit le faire que de manière pertinente. Inutile d'aller avancer des compétences dont le niveau ne sera pas suffisant pour le projet proposé.</p><p> Il est évident que on ne peux tout maîtriser en sortie de formation, cependant le métier de développeur à ceci de particulier que les courbes d'apprentissages ne se termine jamais; le temps de maîtriser une techno et la voilà supplanté par une autre (c'est d'ailleurs, entre autre, ce qui rend ce métier si intéressant).</p><p>En conclusion, oui un développeur junior peux se présenter en fullstack, mais il doit le faire en connaissance de cause, il est donc très important de bien border la mission en amont.</p>",
    title07:        "Comment se créer son premier réseau ? ",
    date07:         "28-07-2020",
    content07:      "<p>Lorsque l'on débute sa vie professionnelle, il y a un élément que l'on a tendance à occulter: c'est l'importance du réseau. Malheureusement, pour de nombreuses personnes, et je suis sûr que vous en connaissez, le <i>réseau</i> possède une connotation négative, faisant référence a des ,<i>magouilles</i> et du <i>pistonnage</i>. Pourtant, <bn>vous ne rencontrez personne qui n'a pu entreprendre quelque chose sans un bon réseau</b>, par <i>bon réseau</i> j’entends <i>qui correspond au besoin</i>, ce même si la dite personne s'en défend de bonne foi.</p><p> Avant tout il convient de <i>remettre l'église au milieu du village</i> en définissant les contours de ce mot souvent mal compris, seulement ensuite nous pourrons nous poser la question de la constitution de son premier réseau.</p><p> Tout d’abord, qu'est-ce que signifie ce mot ? Le Larousse, dans sa définition littéraire, donne <i>Ensemble de liens, d'attaches de tous ordres</i>, nul mentions ici de <i>pistonnage</i>, <i>magouilles</i> ou autre, il s'agit simplement d'un <i>ensemble de liens</i>. Il est important de se détacher de toute connotations négatives qui ne sont pas en rapport direct avec le sujet. Néanmoins il ne s’agit pas non plus de nier ce qui existe. <b>Si le réseau est l'action alors le reste est la conséquence</b>, ou plutôt <i>une</i> conséquence. De manière basique, un réseau représente les personnes qui nous entoure, ce que l'on en fait est autre chose. Remarquez qu'il n'est pas question ici de bonnes ou de mauvaises relations mais juste de relations. Nous viendrons à leurs qualités par la suite.</p><p>Ceci posé, le voile commence déjà à se soulever pour celles et ceux qui se demande comment réussir sans réseau. Les esprits avisés auront compris que la question initiale est mal posée, en effet il n'est pas utile de créer son premier réseau puisque celui-ci existe de fait, comprenant les personnes qui nous entourent.</p><p>Faisons un rapide comptage, à commencer par le premier de tous: la famille, puis la famille de la famille (grands parents, cousin(e)s etc. ), mettons une dizaine (déjà !), ajoutons les ami(e)s, mettons une autre dizaine, puis les collègues (une autre dizaine); a ce stade nous sommes a une trentaine avant même d'avoir commencer à travailler le sujet. On peux se dire que cette trentaine de personne exclue déjà l’hypothèse de la solitude; à présent nous pouvons étirer un peu tout ça, reprenons au début: la famille (au sens large) a, elle même, des amis, si on reste sur les amis suffisamment proches pour être atteignables et qu' on part sur une famille de 4 personnes en plus de vous (et pour simplifier partons du principe que les couples comptent pour un pour les amis), on arrive à 90, on peut additionner également la famille des amis, en ne gardant que la famille directe on ajoute 100 personnes. Uniquement dans le cercle très proche, sans effort, nous sommes à 190. Enfin il convient d'ajouter les collègues, une autre dizaine soit un total de 200</p><p>Arrêtons nous là, vous avez compris le principe. Ce total de 200 peux sembler très important mais comme vous l’avez constaté on y arrive très vite et il s'agit des 200 premières personnes qui peuvent vous aider, vous conseiller, vous présenter d'autres personnes. Avec un peu d’effort, mais à peine, nous pouvons ajouter les amis, voir famille des collègues, tous ces gens que vous pouvez ajouter également dans votre réseaux social préféré auquel s'ajoutera leurs propres relations. C'est ainsi que nous arrivons a la célèbre règle des 6 degrés de séparation (<a href=\"https://fr.wikipedia.org/wiki/Six_degr%C3%A9s_de_s%C3%A9paration\" target=_blank>cliquer ici pour plus de détails</a>).</p><p>L’étape suivante est de qualifier tout ce monde. D’abord distinguer les formes d’aides : morale, financière, conseil etc., chacun des individus comptés ci-dessus peuvent être mis dans une ou plusieurs de ces catégories, ensuite il convient de rayer de la liste toutes les personnes avec qui aucune relation n’est possible, <b>la dernière étape consiste à OSER</b>. Appelez, demandez, informez, vous serez surpris des réponses et en bonus vous gagnerez en confiance … et vous pourrez l’étendre encore.</p><p>Pour conclure, je dirai qu’<b>il est faux de penser que, parce que trop jeune en âge ou professionnellement, on a pas de réseau, en revanche il est possible de ne pas en être conscient et/ou de ne savoir l'utiliser.</b></p>",
    title08:        "Comment gérer au mieux le télétravail ? ",
    date08:         "11-08-2020",
    content08:      "<p>Depuis le confinement que nous avons subi, la question du télétravail jusqu’alors fort mal vu dans une France habituée à faire rimer présence et performance, deviens alors centrale.</p><p> Bouleversement dans les habitudes de travail, des gérants d’abord, contraints d’adapter leurs modes de managements, et des salariés ensuite, contraints eux aussi de subir l’intrusion de leur vie professionnelle dans leur vie privée.</p><p>Dès lors, la question est posée : <b>Comment gérer au mieux le télétravail ?</b></p><p>Une question à deux points de vue, celle du manager et celle du salarié.</p><p> Celle du manager tout d’abord. En tout premier lieu, un changement de paradigme s’impose, <b>passer de l’équation « performance = présence » à « mission = efficacité »</b>; cela demande de faire <b>confiance</b> à son salarié, se rappeler qu’il n’est pas là pour être au service de l’entreprise mais pour engager une mission, ceci posé on remarque tout de suite qu’il faut donc poser proprement la mission, et de fait l’objectif ne devient plus la gestion du présentiel mais la mise en œuvre de ladite mission; il s’agit de s’assurer que le salarié puisse avoir chez lui les bons outils, lui demander s’il a besoin de quelque chose: ordinateur, bonne chaise (oui, la chaise est fondamentale et tout le monde n’a pas une chaise de bureau chez lui), une bonne souris etc.; enfin il y a la question du management, organiser une visio hebdomadaire pour faire un point, savoir poser les bonnes questions: où en es-tu de ta mission ? Quelles sont les difficultés rencontrées ? Comment puis-je t’aider ? Dans la semaine appeler au moins une fois, afin de s’assurer que tout aille bien. En un mot confiance.</p><p>Ensuite, le point de vue du salarié. Là aussi un changement de paradigme s’impose. Là où la stricte séparation de la vie professionnelle et de la vie personnelle était factuelle, elle devient inexistante, aussi il faut la recréer. Tout d’abord, essayer d’avoir, dans la mesure du possible, <b>un espace de travail</b>, une pièce à part dans l’idéal, un petit bureau dans le salon, peut-être ajouter un tapis sous cet espace afin d’avoir une barrière psychologique; ensuite s’assurer d’avoir une bonne chaise, une vraie chaise de bureau est l’idéal, au delà du confort qu’elle procure, elle dit aussi <i>si tu est assis là c’est que tu es dans ton espace de travail</i>; également les outils, un ordinateur portable, une souris correct, les bons logiciels etc. Enfin l’organisation, se donner une heure de début et une heure de fin, ne pas hésiter/oublier de s’arrêter pour manger, gérer ses todo lists et donc savoir la veille ce que l’on va faire le lendemain, gérer les enfants et les animaux de compagnies de la même manière que si on devait partir. Pour finir, ne pas hésiter à appeler un collègue ou son manager en cas de besoin ou même de sentiment de solitude.</p><p>Le télétravail ne coule pas de source pour tout le monde, ce qui est bien normal, cependant, subi ou souhaité, il va se généraliser pour des tas de raisons qui rendraient ce post trop long, aussi autant le voir comme une opportunité de rendre sa vie meilleure.</p><p><b>Adaptation, le maître mot de tout changement.</b></p>"
}

