import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../components/Main'
import Cv from '../components/Cv'
import Portfolio from '../components/Portfolio'
import Blog from '../components/Blog'

Vue.use(VueRouter);

export default new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: Main,
      name: 'Main'
    },
    {
      path: '/cv',
      component: Cv,
      name: 'Cv'
    },
    {
      path: '/portfolio',
      component: Portfolio,
      name: 'Portfolio'
    },
    {
      path: '/blog',
      component: Blog,
      name: 'Blog'
    },    
    {
      path: '/validate_main',
    },

  ],
})